<template>
    <div v-if="ready">
        <div class="card" style='margin-bottom:1rem'>
            <div class="card-body card-block" style='overflow-y:auto;padding:0;'>
                <v-data-table
                v-if="(ready_table)&&(rep_type=='handler')"
                :headers="headers"
                :items="pooldata"
                class="elevation-1"
                :rows-per-page-items="[item_count]"
                disable-initial-sort>
                    <template v-slot:items="props">
                        <td><input type='checkbox' class='form-control' v-model="selected_id" :value="props.item.item_id" @change="display_selected_ticket"/></td>
                        <td class='relative'><a href="" :id= props.item.item_id v-on:click.prevent="editform(props.item.item_id)">Details <i v-if="props.item.imgstat=='true'" class="attachment fa fa-paperclip" aria-hidden="true"></i></a></td>
                        <td style='width:5em'>{{ props.item.ref_id }}</td>
                        <td>{{ props.item.Req_Name }}</td>
                        <td style='width:55em'>{{ props.item.Description }}</td>
                        <td>{{ props.item.Location }}</td>
                        <td>{{ props.item.Due_date }}</td>
                        <td style='width:5em'>{{ props.item.Due_time }}</td>
                        <td>{{ props.item.Handler }}</td>
                        <td>{{ props.item.Status }}</td>            
                        <template v-slot:pageText="props">
                            Items {{ props.pageStart }} - {{ props.pageStop }} out of {{ props.itemsLength }}
                        </template>    
                    </template>
                </v-data-table>
                <v-data-table
                v-if="(ready_table)&&(rep_type=='applicant')"
                :headers="headers_app"
                :items="pooldata"
                class="elevation-1"
                :rows-per-page-items="[item_count]"
                disable-initial-sort>
                    <template v-slot:items="props">
                        <td style='width:5em'>{{ props.item.item_id }}</td>
                        <td>{{ props.item.Req_Name }}</td>
                        <td style='width:55em'>{{ props.item.Description }}</td>
                        <td>{{ props.item.Location }}</td>
                        <td>{{ props.item.Due_date }}</td>
                        <td style='width:5em'>{{ props.item.Due_time }}</td>
                        <td>{{ props.item.Handler }}</td>
                        <td>{{ props.item.Status }}</td>            
                        <template v-slot:pageText="props">
                            Items {{ props.pageStart }} - {{ props.pageStop }} out of {{ props.itemsLength }}
                        </template>    
                    </template>
                </v-data-table>
            </div>
            <div class="card-footer" style='padding-top:0.2rem;padding-bottom:0.2rem;' v-if="rep_type=='handler'">
                <button type="button" class="btn btn-primary btn-sm" v-if="collapse" @click="expand_table">
                    <i class="fa fa-arrow-down"></i> Expand
                </button>
                <button type="button" class="btn btn-primary btn-sm" v-if="!collapse" @click="collapse_table">
                    <i class="fa fa-arrow-up"></i> Collapse
                </button>
            </div>
        </div>
        <div v-if="ticket" class="card" style='margin-top:1rem;max-height:44vh!important;margin-bottom:1rem'>
            <div class="card-header">
                <strong>Support Tickets</strong>
            </div>            
            <div class="card-body card-block" style='overflow-y:auto' id="container">
                <div v-for="n in ticket_length">
                    <div v-if="ticket_data[n-1].rep_type=='handler'">
                        <div class="col-8" style='margin-top:0.25rem'>
                            <textarea readonly class='form-control dpbox' style='resize:none;' rows="4">#{{ n }} RE: {{ ticket_data[n-1].rep_grp }}&#13;&#10;{{ ticket_data[n-1].message }}</textarea>
                            <div class="flex col-12 px0" style='margin-top:0.15rem'>
                                <div class='col-6 px0' style='font-size:0.7rem'>
                                    Replied by: <b>{{ ticket_data[n-1].rep_by_name }}</b>
                                </div>
                                <div class='col-6 px0' style='font-size:0.7rem;text-align:right'>
                                    Replied on: <b>{{ ticket_data[n-1].rep_date }}  {{ ticket_data[n-1].rep_time }}</b>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div v-if="ticket_data[n-1].rep_type=='applicant'">
                        <div class="col-8" style='margin-left:auto;margin-top:0.25rem'>
                            <textarea readonly class='form-control dpbox' style='resize:none;' rows="4">#{{ n }} RE: {{ ticket_data[n-1].rep_grp }}&#13;&#10;{{ ticket_data[n-1].message }}</textarea>
                            <div class="flex col-12 px0" style='margin-top:0.15rem'>
                                <div class='col-6 px0' style='font-size:0.7rem'>
                                    Replied by: <b>{{ ticket_data[n-1].rep_by_name }}</b>
                                </div>
                                <div class='col-6 px0' style='font-size:0.7rem;text-align:right'>
                                    Replied on: <b>{{ ticket_data[n-1].rep_date }}  {{ ticket_data[n-1].rep_time }}</b>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div class="card" style='margin-top:1rem;max-height:25vh!important'>
            <div class="card-header">
                <strong>Reply to Request ID: <span v-for="sel in selected_id.length">{{ selected_id[sel-1] }} </span></strong>
            </div>            
            <div class="card-body card-block">
                <div class="col-12 flex px0">
                    <div class="col-2">
                        <label>Follow-up</label>
                    </div>
                    <div class="col-10">
                        <textarea class="form-control" placeholder="Enter Message" v-model="reply_msg"></textarea>
                    </div>
                </div>
                <div class="col-12 flex px0" v-if="rep_type=='handler'" style='margin-top:0.5rem'>
                    <div class="col-2">
                        <label>Status</label>
                    </div>
                    <div class="col-10">
                        <select class="form-control select" v-model="status">
                            <option>Not Started</option>
                            <option>In Progress</option>
                            <option>Completed</option>
                            <option>Waiting on something else</option>
                            <option>Re-Opened</option>
                            <option>Cancelled</option>
                            <option>Rejected</option>
                        </select>
                    </div>
                </div>
            </div>
            <div class="card-footer">
                <button type="button" class="btn btn-primary btn-sm" @click="update_message">
                    <i class="fa fa-paper-plane"></i> Submit
                </button>
                <button type="button" class="btn btn-danger btn-sm" @click="return_prev">
                    <i class="fa fa-undo"></i> Return
                </button>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    data(){
        return{
            rep_type:'handler',
            reply_msg:'',
            ready_table:false,
            collapse:true,
            item_count:1,
            max_length:0,
            re_msg:'',
            selected_id:[],
            status:'',
            record_clr:[],
            ticket_length:0,
            ticket_data:[],
            ticket:false,
            pooldata:[],
            record_id:'',
            ready:false,
            headers: [
                { text: 'Action',value :'item_id',sortable: false},
                { text: 'Details',value :'item_id',sortable: false},
                { text: 'ID',value :'item_id',sortable: false},
                { text: 'Requester',value:'Req_Name',sortable: false},
                { text: 'Description',value:'Description',sortable: false},
                { text: 'Location',value:'Location',sortable: false},
                { text: 'Due date',value:'Due_date',sortable: false},
                { text: 'Due Time',value:'Due_time',sortable: false},
                { text: 'Assigned to',value:'Handler',sortable: false},
                { text: 'Issue Status',value:'Status',sortable: false},
            ],
            headers_app: [
                { text: 'ID',value :'item_id',sortable: false},
                { text: 'Requester',value:'Req_Name',sortable: false},
                { text: 'Description',value:'Description',sortable: false},
                { text: 'Location',value:'Location',sortable: false},
                { text: 'Due date',value:'Due_date',sortable: false},
                { text: 'Due Time',value:'Due_time',sortable: false},
                { text: 'Assigned to',value:'Handler',sortable: false},
                { text: 'Issue Status',value:'Status',sortable: false},
            ],
        }
    },
    async created(){
        window.onbeforeunload = function (e) {
            e = e || window.event;
            if (e) {
                e.returnValue = 'Changes you made may not be saved.';
            }
            return 'Changes you made may not be saved.';
        }
        if(document.getElementById('tmp_id').value==undefined){
            this.$alert('No Record Selected')
                .then(
                    ()=>{
                        this.$router.push('IR');
                    }
                )
        }
        this.rep_type = document.getElementById('tmp_id_2').value;
        this.record_id = document.getElementById('tmp_id').value;
        await this.fetch_record();
        await this.fetch_ticket();
        this.ready = true;      
    },
    async mounted(){
        this.$forceUpdate();
    },
    updated(){
        if(this.ticket){
            var container = document.getElementById('container');
            container.scrollTop = container.scrollHeight;
        }
    },  
    destroyed(){
      window.onbeforeunload=null;
    },
    methods:{
        async return_prev(){
            this.$router.go(-1);
        },
        editform(event){
            var targetID = event;
            document.getElementById('tmp_id').value = targetID;
            //document.getElementById('query').value = this.search_key;
            //this.$router.push('edit_form');
            this.$router.push('edit_form');
        },
        async expand_table(){
            this.ready_table = false;
            await this.set_length();
            this.collapse=!this.collapse;
            this.ready_table = true;
            this.$forceUpdate();
        },
        async collapse_table(){
            this.ready_table = false;
            await this.set_length();
            this.collapse=!this.collapse;          
            this.ready_table = true;
            this.$forceUpdate();
        },
        set_length(){
            var leng = 0;
            if(this.collapse){
                if(this.max_length>5){
                    leng = 5;
                } else {
                    leng = this.max_length;
                }
                this.item_count = leng;
            } else {
                this.item_count = 1;
            }
        },
        async fetch_record(){
            var formData = new FormData();
            formData.append('record_id',this.record_id);
            formData.append('rep_type',this.rep_type);
            await axios.post('RequestPortal/backend/fetch_record_header.php',formData)
                .then(
                    (response)=>{
                        this.pooldata = response.data;
                        this.status = response.data[0].Status;
                        this.selected_id[0] = document.getElementById('tmp_id').value;
                        this.max_length = response.data.length;
                        this.re_msg = response.data[0].Description;
                    }
                )
            this.ready_table = true;
        },
        async fetch_ticket(){
            var formData = new FormData();
            formData.append('record_id',this.record_id);
            await axios.post('RequestPortal/backend/fetch_ticket.php',formData)
                .then(
                    (response)=>{
                        if(response.data!='empty'){
                            this.ticket_data = response.data;
                            this.ticket_length = this.ticket_data.length;
                            this.ticket=true;
                        } else {
                            this.ticket=false;
                        }
                    }
                ) 
        },
        async display_selected_ticket(){
            var formData = new FormData();
            formData.append('select_id',this.selected_id);
            await axios.post('RequestPortal/backend/fetch_ticket.php',formData)
                .then(
                    (response)=>{
                        if(response.data!='empty'){
                            this.ticket_data = response.data;
                            this.ticket_length = this.ticket_data.length;
                            this.re_msg = response.data[0].Description;
                            this.ticket=true;
                        } else {
                            this.ticket=false;
                        }
                    }
                )
        },
        update_message(){
            var ready_for_submit = false;
            if((this.status!='Completed')&&(this.status!='In Progress')){
                if(this.reply_msg.length>0){
                    ready_for_submit = true;
                } else {
                    ready_for_submit = false;
                }
            } else {
                ready_for_submit = true;
            }
            if(ready_for_submit){
                var formData = new FormData();
                formData.append('select_id',this.selected_id);
                formData.append('msg',this.reply_msg);
                formData.append('stat',this.status);
                formData.append('rep_type',this.rep_type);
                axios.post('RequestPortal/backend/submit_message.php',formData)
                    .then(
                        (response)=>{
                            if(response.data=='OK'){
                                this.$alert('Successfully Submitted');
                                document.getElementById('query').value='';
                                this.$router.go(-1);
                            } else if(response.data='Session Time Out'){
                                this.$alert('Session Time Out');
                                this.$router.push('PortalHome');
                            } else {
                                console.log(response.data);
                            }
                        }
                    )
                } else {
                    this.$alert('Please fill in message box');
                }
            }
    }
}
</script>
<style scoped>
.flex{
    display:flex;
    flex-direction:row;
}
.px0{
    padding-left:0;
    padding-right:0;
}
.dpbox:first-line { 
    font-weight: bold!important; 
}
</style>


